router.get("/groups-students/:idEncadrant", async (req, res) => {
  const { idEncadrant } = req.params;
  const idEncadrantNum = parseInt(idEncadrant, 10);
  if (isNaN(idEncadrantNum)) {
    return res.status(400).json({ error: "idEncadrant must be a valid number" });
  }

  try {
    const query = `...`; // Your existing query
    const [rows] = await db.promise().query(query, [idEncadrantNum]);
    if (rows.length === 0) {
      return res.status(404).json({ message: "No students found for this jury member" });
    }
    // Convert grades to numbers or undefined for frontend
    const students = rows.map((row) => ({
      ...row,
      noteRapport: row.noteRapport != null && !isNaN(Number(row.noteRapport)) ? Number(row.noteRapport) : undefined,
      notePresentation: row.notePresentation != null && !isNaN(Number(row.notePresentation)) ? Number(row.notePresentation) : undefined,
      noteDiscussion: row.noteDiscussion != null && !isNaN(Number(row.noteDiscussion)) ? Number(row.noteDiscussion) : undefined,
      noteSavoirFaireSavoirEtre: row.noteSavoirFaireSavoirEtre != null && !isNaN(Number(row.noteSavoirFaireSavoirEtre)) ? Number(row.noteSavoirFaireSavoirEtre) : undefined,
      noteTotale: row.noteTotale != null && !isNaN(Number(row.noteTotale)) ? Number(row.noteTotale) : undefined,
    }));
    res.json(students);
  } catch (error) {
    console.error("SQL Error in GET /groups-students:", error.message);
    res.status(500).json({ error: "Server error fetching student data", details: error.message });
  }
});